.preferences {
  @include grid-template;

  &__nav {
    grid-column: 4 / 10;

    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__cards {
    grid-column: 1 / -1;
    grid-row: 2;

    @include grid-template;

    @include on-desktop {
      margin-top: 62px;
    }
  }

  &__card-pref {
    &--design {
      grid-column: 1 / -1;

      @include on-tablet {
        grid-column: 1 / 5;
        grid-row: 1;
      }

      @include on-desktop {
        grid-column: 1 / 4;
      }
    }

    &--speaker {
      @include on-tablet {
        grid-column: -5 / -1;
        grid-row: 1;
      }

      @include on-desktop {
        grid-column: 4 / 7;
      }
    }

    &--multiroom {
      @include on-tablet {
        grid-column: 1 / 5;
        grid-row: 2;

        margin-top: 65px;
      }

      @include on-desktop {
        grid-column: 7 / 10;
        grid-row: 1;

        margin-top: 0;

      }
    }

    &--lightning {
      @include on-tablet {
        grid-column: -5 / -1;
        grid-row: 2;

        margin-top: 65px;
      }

      @include on-desktop {
        grid-column: -4 / -1;
        grid-row: 1;

        margin-top: 0;
      }
    }
  }
}
