.testimonials {
  display: flex;
  justify-content: center;

  &__wrap {
    width: 260px;

    padding-top: 50px;

    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;

    @include on-tablet {
      padding-top: 60px;

      width: 700px;
    }

    @include on-desktop {
      padding-top: 42px;

      width: 786px;
    }
  }

  &__backg-img {
    width: 208px;
    height: 171px;

    position: absolute;
    top: 0;
    left: 26px;

    z-index: -1;

    opacity: 0.93;

    background-image: url(../images/testimonials/quotes.png);

    @include set-pos-backg;

    @include on-tablet {
      width: 233px;
      height: 189px;

      left: 233px;
    }

    @include on-desktop {
      width: 233px;
      height: 192px;

      left: 277px;
    }
  }

  &__quote {
    margin: 0;

    @include font-quote;
  }

  &__person {
    margin-top: 30px;
    width: 120px;

    display: flex;
    flex-direction: column;
    align-items: center;

    @include on-tablet {
      width: 174px;
      margin-top: 40px;
    }

    @include on-desktop {
      margin-top: 30px;
    }
  }

  &__person-photo {
    width: 62px;
    height: 62px;

    background-image: url(../images/testimonials/ebalo.png);

    @include set-pos-backg;

    border-radius: 50%;
  }

  &__person-name {
    @include font-h5;
  }

  &__person-position {
    @include font-secondary;

    text-align: center;
  }
}
