.meet-luna {
  display: flex;
  justify-content: center;
  height: 195px;

  @include on-tablet {
    height: 430.16px;
  }

  @include on-desktop {
    height: 520px;
  }

  &__wrap {
    @include grid-template;

    position: relative;
  }

  &__img {
    position: absolute;
    top: 0;
    left: -18px;

    width: 296px;
    height: 195px;

    background-image: url(../images/luna-back.png);

    @include set-pos-backg;

    @include on-tablet {
      position: static;

      width: 700px;
      height: 430.16px;

      grid-column: 1 / -1;
    }

    @include on-desktop {
      grid-column: 3 / -3;

      width: 846px;
      height: 520px;
    }
  }
}
