.feature {
  display: flex;
  flex-direction: column;

  @include on-tablet {
    width: 260px;
  }

  @include on-desktop {
    width: 378px;
  }

  &--hidden {
    display: none;

    @include on-desktop {
      display: flex;
    }
  }

  &__title {
    @include font-h3;

    margin: 0;

    text-transform: uppercase;
  }

  &__list {
    margin: 0;
    padding: 0;

    margin-top: 20px;

    display: flex;
    flex-direction: column;
    gap: 10px;

    @include on-desktop {
      margin-top: 30px;
    }
  }

  &__item {
    @include font-secondary;

    margin-left: 16px;

    color: $black-main;

    @include on-desktop {
      @include font-main;
    }
  }

  &__slider {
    margin-top: 50px;
    align-self: center;
  }
}
