.luna-eye {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include on-tablet {
    height: 415px;
  }

  @include on-desktop {
    height: 388px;
  }

  &__wrap {
    @include grid-template;

    position: relative;
  }

  &__img {
    grid-column: 1 / -1;

    margin-top: 30px;

    width: 259px;
    height: 210px;

    background-image: url(../images/luna-eye.png);

    @include set-pos-backg;

    @include on-tablet {
      width: 374px;
      height: 415px;

      margin-top: 0;

      position: absolute;
      top: 0;
      right: -34px;
    }

    @include on-desktop {
      width: 582px;
      height: 388px;
      right: 0;
    }
  }

  &__title {
    grid-column: 1 / -1;

    text-transform: uppercase;

    @include on-tablet {
      grid-column: 1 / 5;
    }

    @include on-desktop {
      grid-column: 1 / 7;
      grid-row: 1 / 2;
    }

    @include font-h2;
  }

  &__description {
    margin-top: 20px;

    grid-column: 1 / -1;

    @include font-secondary;

    @include on-tablet {
      margin-top: 30px;

      @include font-main;

      grid-column: 1 / 5;
    }

    @include on-desktop {
      grid-column: 1 / 7;
      grid-row: 2 / 3;
    }
  }

  &__button {
    width: fit-content;
    margin-top: 30px;
    grid-column: 1 / -1;

    text-decoration: none;

    @include hover-scale(1.02);

    &::after {
      display: block;
      content: '';

      margin-top: 5px;

      height: 1px;
      width: 53px;

      background-color: #0DB2B3
    }

    @include font-button;

    @include on-tablet {
      margin-top: 60px;

      grid-column: 1 / 5;
    }

    @include on-desktop {
      grid-column: 1 / 7;
      grid-row: 3 / 4;
    }
  }
}
