.nav {
  &__wrapper {
    height: 155px;

    @include on-tablet {
      height: 232px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    display: flex;
    flex-direction: row;
    justify-content: space-between;

    list-style-type: none;

    &--column {
      flex-direction: column;
      height: 100%;
    }
  }

  &__item {
    width: 101px;
  }

  &__link {
    display: block;
    text-decoration: none;
    color: $black-main;

    font-family: $font-inter;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    @include hover-scale(1.05);

    @include on-desktop {
      font-family: $font-inter;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
    }

    &:hover {
      font-weight: 700;
      color: #131313;
    }

    &:focus {
      font-weight: 700;
      color: #131313;
    }
  }
}
