.questions {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__wrap {
    height: 413px;

    @include grid-template;

    @include on-tablet {
      height: 339px;
      grid-template-rows: 92px 1fr;
    }

    @include on-desktop {
      height: 273px;
      grid-template-rows: 140px 1fr;
    }
  }

  &__title {
    width: 260px;
    grid-column: 1 / -1;

    @include font-h2;

    text-transform: uppercase;

    @include on-tablet {
      width: 339px;
      grid-column: 1 / 5;
      grid-row: 1 / 2;
    }

    @include on-desktop {
      width: 584.2px;
      grid-column: 1 / 7;
    }
  }

  &__form {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;

    @include on-tablet {
      grid-column: -5 / -1;
      grid-row: 1 / 3;
    }

    @include on-desktop {
      grid-column: -7 / -1;
      grid-row: 1 / 3;
    }
  }

  &__email {
    margin-top: 27px;

    font-family: $font-inter;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    box-sizing: border-box;
    padding: 12px 20px;

    width: 260px;
    height: 48px;
    border: 1px solid #828282;
    outline: none;

    color: $black-main;

    &::placeholder {
      color: #BDBDBD;
    }

    @include on-tablet {
      margin-top: 0;
      padding-left: 30px;

      width: 340px;
    }

    @include on-desktop {
      padding-left: 36.37px;
      width: 581.94px;
    }
  }

  &__message {
    font-family: $font-inter;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    resize: none;

    margin-top: 20px;

    box-sizing: border-box;
    padding-left: 20px;
    padding-top: 12px;

    width: 260px;
    height: 128px;
    border: 1px solid #828282;

    color: $black-main;
    outline: none;

    &::placeholder {
      color: #BDBDBD;
    }

    @include on-tablet {
      margin-top: 20px;
      padding-left: 30px;

      width: 340px;
      height: 201px;
    }

    @include on-desktop {
      margin-top: 19px;
      padding-left: 36.37px;

      width: 581.94px;
      height: 128px;
    }
  }

  &__button {
    width: 102px;
    height: 40px;

    @include hover-scale(1.02);

    margin-top: 30px;

    display: flex;
    justify-content: center;
    align-items: center;

    font-family: $font-inter;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    color: #fff;
    text-transform: uppercase;

    background-color: #0DB2B3;

    border: none;


    cursor: pointer;
  }
}
