.button {
  width: 100px;
  height: 48px;

  display: none;

  @include on-tablet {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  background-color: $black-main;

  font-family: 'Helvetica Neue', sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 14.65px;
  color: #fff;

  cursor: pointer;

  @include hover-scale(1.04);

  &--long {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 260px;

    @include on-tablet {
      display: none;
    }
  }
}
