.bottom {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;

    width: 260px;

    @include on-tablet {
      width: 700px;
    }

    @include on-desktop {
      width: 1194px;
    }
  }

  &__logo  {
    width: 184px;
    height: 25px;

    display: none;

    background-image: url(../images/bottom/logo-tab.png);

    @include set-pos-backg;
    @include hover-scale(1.1);

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      width: 161px;
      height: 26px;

      background-image: url(../images/bottom/logo-desk.png);
    }
  }

  &__socials {
    width: 152px;

    display: flex;
    gap: 40px;

    @include on-tablet {
      margin-top: 60.7px;
      gap: 49px;

      width: 170px;
    }

    @include on-desktop {
      width: 152px;
      gap: 40px;
    }
  }

  &__social {
    width: 24px;
    height: 24px;

    @include set-pos-backg;
    @include hover-scale(1.1);

    &--facebook {
      background-image: url(../images/socials/facebook.png);
    }

    &--twitter {
      background-image: url(../images/socials/twitter.png);
    }

    &--instagram {
      background-image: url(../images/socials/instagram.png);
    }
  }

  &__terms {
    @include font-secondary;

    text-align: center;

    margin-top: 30px;

    @include on-tablet {
      margin-top: 59.79px;
    }

    @include on-desktop {
      margin-top: 60px;
    }
  }

  &__home {
    width: 24px;
    height: 24px;

    position: absolute;
    top: 0;
    right: 0;

    display: none;

    background-image: url(../images/socials/home.png);

    @include set-pos-backg;
    @include hover-scale(1.1);

    @include on-tablet {
      display: block;
    }
  }
}
