.top-bar {
  height: 74px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  &__logo-link {
    @include hover-scale(1.1);
  }

  &__logo {
    width: 161px;
    height: 26px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 30px;
  }

  &__specs {
    width: 45px;
    height: 17px;
    background-image: url(../images/top-bar/specs.svg);

    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    display: none;

    cursor: pointer;

    @include hover-scale(1.1);

    @include on-tablet {
      display: block;
    }
  }

  &__en-ua {
    width: 49px;
    height: 18px;

    background-image: url(../images/top-bar/en-ua.png);

    @include set-pos-backg;
    @include hover-scale(1.1);

    display: none;
    cursor: pointer;

    @include on-tablet {
      display: block;
    }
  }

  &__burger-menu {
    display: block;

    width: 32px;
    height: 32px;
    background-image: url(../images/top-bar/burger.png);

    @include set-pos-backg;
    @include hover-scale(1.1);

    @include on-desktop {
      display: none;
    }
  }

  &__close-menu {
    display: block;

    width: 32px;
    height: 32px;

    background-image: url(../images/top-bar/close-menu.png);

    @include set-pos-backg;
    @include hover-scale(1.1);
  }
}
