.card-pref {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include hover-scale(1.05);

  width: 260px;
  height: 254px;

  @include on-tablet {
    width: 340px;
    height: 243px;
  }

  @include on-desktop {
    width: 276px;
    height: 286px;
  }

  &--hide {
    display: none;

    @include on-tablet {
      display: flex;
    }
  }

  &__img  {
    @include set-pos-backg;

    &--design {
      width: 50px;
      height: 40px;

      margin-bottom: 40px;

      background-image: url(../images/prefs/design-tab.png);

      @include on-tablet {
        margin-bottom: 50px;
      }

      @include on-desktop {
        background-image: url(../images/prefs/design.png);

        margin-bottom: 30px;

        width: 70px;
        height: 50px;
      }
    }

    &--speaker {
      width: 46px;
      height: 40px;

      @include on-tablet {
        background-image: url(../images/prefs/speaker-tab.png);

        margin-bottom: 50px;
      }

      @include on-desktop {
        background-image: url(../images/prefs/speaker.png);

        margin-bottom: 30px;

        width: 70px;
        height: 50px;
      }
    }

    &--multiroom {
      width: 43px;
      height: 37px;
      background-image: url(../images/prefs/multiroom-tab.png);

      @include on-tablet {
        margin-bottom: 44.19px;
      }

      @include on-desktop {
        width: 70px;
        height: 50px;

        margin-bottom: 30px;

        background-image: url(../images/prefs/multiroom.png);
      }
    }

    &--lightning {
      width: 44px;
      height: 44px;

      background-image: url(../images/prefs/lightning-tab.png);

      @include on-tablet {
        margin-bottom: 37px;
      }

      @include on-desktop {
        width: 70px;
        height: 50px;

        margin-bottom: 30px;

        background-image: url(../images/prefs/lightning.png);
      }
    }
  }

  &__name {
    margin: 0;

    @include font-h4;

    @include on-desktop {
      width: 276px;
      height: 56px;
    }
  }

  &__info {
    margin: 0;
    margin-top: 20px;

    @include font-secondary;

    text-align: center;

    @include on-desktop {
      margin-top: 24px;
    }
  }
}
