.slider {
  width: 93px;
  height: 47px;

  display: flex;
  flex-direction: column;
  align-items: center;

  @include on-tablet {
    align-self: start;
  }

  @include on-desktop {
    display: none;
  }

  &__left-right {
    position: relative;
    width: 100%;
    height: 14px;
  }

  &__left {
    position: absolute;
    width: 48px;
    height: 13px;

    top: 1px;
    left: 0;

    background-image: url(../images/slider/left-arrow.png);

    @include set-pos-backg;

    &:hover {
      background-image: url(../images/slider/left-arrow-hover.png);
    }
  }

  &__right {
    position: absolute;
    width: 48px;
    height: 13px;

    right: 0;
    top: 0;

    @include set-pos-backg;

    background-image: url(../images/slider/right-arrow.png);

    &:hover {
      background-image: url(../images/slider/right-arrow-hover.png);
    }
  }

  &__count {
    margin-top: 8px;

    width: 43px;
    height: 24px;

    background-image: url(../images/slider/count.png);

    @include set-pos-backg;
  }
}
