.asside {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  z-index: 2;

  display: flex;
  flex-direction: column;
  align-items: center;

  height: 100vh;

  pointer-events: none;
  visibility: hidden;

  background-color: #F7F7F7;

  @include on-desktop {
    display: none;
  }

  &:target {
    pointer-events: all;
    visibility: visible;
  }

  &__wrap {
    @include grid-template;

    position: relative;
    padding-bottom: 30px;

    height: inherit;
  }

  &__top-bar {
    grid-column: 1 / -1;
  }

  &__nav {
    margin-top: 31px;

    @include on-tablet {
      margin-top: 40px;
    }

    grid-column: 1 / -1;
  }

  &__specs-enua {
    grid-column: 1 / -1;
    margin-top: 50px;

    display: flex;

    @include on-tablet {
      display: none;
    }
  }

  &__specs {
    width: 45px;
    height: 17px;

    background-image: url(../images/top-bar/specs.svg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    @include hover-scale(1.1);
  }

  &__enua {
    width: 50px;
    height: 18px;

    margin-left: 20px;

    background-image: url(../images/top-bar/en-ua.png);

    @include set-pos-backg;
    @include hover-scale(1.1);
  }

  &__button {
    position: absolute;
    left: 0;
    bottom: 30px;
  }
}
