.header {
  display: flex;
  justify-content: center;

  &__wrap {
    position: relative;

    @include grid-template;
  }

  &__top-bar {
    grid-column: 1 / -1;
  }

  &__main {
    grid-column: 1 / -1;

    display: flex;
    flex-direction: column;

    @include on-tablet {
      @include grid-template;
    }
  }

  &__img {
    width: 320px;
    height: 245px;

    position: absolute;
    top: 76px;
    left: -30px;

    background-image: url(../images/top-bar/header-img-mob.png);

    @include set-pos-backg;

    @include on-tablet {
      width: 429px;
      height: 317px;

      background-image: url(../images/top-bar/header-img-tab.png);

      inset: auto 0 0 auto;
    }

    @include on-desktop {
      width: 816px;
      height: 464px;

      right: -4px;
      background-image: url(../images/top-bar/header-img-desk.png);
    }
  }

  &__title {
    margin: 0;
    margin-top: 285px;

    @include font-h1;

    @include on-tablet {
      margin-top: 117.23px;
      grid-column: 1 / 5;
    }

    @include on-desktop {
      margin-top: 158px;
      grid-column: 1 / 5;
    }
  }

  &__main-text {
    margin-top: 20px;

    @include font-main;

    @include on-tablet {
      grid-column: 1 / 4;
      margin-top: 30px;
    }
  }
}
