.features {
  display: flex;
  flex-direction: column;
  align-items: center;

  position: relative;

  height: 647px;

  @include on-tablet {
    height: 626.5px;
  }

  @include on-desktop {
    height: 752px;
  }

  &__wrap {
    position: relative;
    height: 575px;

    @include grid-template;

    margin-top: 32px;
    padding-top: 30px;

    @include on-tablet {
      height: 556.5px;
      margin-top: 30px;
      padding-top: 60px;
    }

    @include on-desktop {
      height: 642px;
    }
  }

  &__background {
    width: 100vw;
    height: 575px;
    position: absolute;
    top: 72px;

    background-color: #D6ECEC;

    @include on-tablet {
      width: 700px;
      height: 556.5px;
      top: 70px;
      left: calc((100vw - 700px) / 2);
    }

    @include on-desktop {
      width: 1194px;
      height: 642px;
      top: 110px;
      left: calc((100vw - 1194px) / 2);
    }
  }

  &__title {
    width: 260px;
    text-align: left;

    @include font-h2;

    @include on-tablet {
      width: 700px;
    }

    @include on-desktop {
      width: 1194px;
    }
  }

  &__connectivity {
    grid-column: 2 / 5;
    grid-row: 1;
  }

  &__app {
    grid-column: 7 / 10;
    grid-row: 1;
  }

  &__sound {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / 5 ;
    }

    @include on-desktop {
      margin-top: 56px;
      grid-row: 2;
    }
  }

  &__img {
    width: 320px;
    height: 173px;

    position: absolute;
    bottom: 0;
    right: -30px;

    background-image: url(../images/features/img-phone.png);

    @include set-pos-backg;

    @include on-tablet {
      width: 420px;
      height: 200px;

      right: 0;

      background-image: url(../images/features/img-tablet.png);
    }

    @include on-desktop {
      width: 582px;
      height: 302px;

      right: 0;

      background-image: url(../images/features/img-desk.png);
    }
  }
}
